export const handleOldDataOfDelay = (oldData) => {
  const delayReplyElement = $('#delay-delay-in-reply')
  const delayTextElement = $('#delay-delay-text')
  const typingOnDisplayElement = $('#delay-typing-on-display')

  const delayReplyFor = parseInt(oldData.delayReplyFor, 10) || 0
  const IsTypingOnDisplayChecked = oldData.IsTypingOnDisplayChecked || false

  delayReplyElement.val(delayReplyFor)
  delayTextElement.text(delayReplyFor + ' ' + lang.delay_html_info_sec)
  typingOnDisplayElement.prop('checked', IsTypingOnDisplayChecked)

  delayReplyElement.on('change', function(e) {
    const currentValue = e.target.value
    delayTextElement.text(currentValue + ' ' + lang.delay_html_info_sec)
  })
}

export const handleDataOfDelay = () => {
  const delayReplyFor = $('#delay-delay-in-reply').val()
  const IsTypingOnDisplayChecked = $('#delay-typing-on-display').prop('checked')

  if (parseInt(delayReplyFor, 10) < 0 || parseInt(delayReplyFor, 10) > 60) {
    return Swal.showValidationMessage(lang.delay_html_msg_delay_range)
  }

  return {
    delayReplyFor,
    IsTypingOnDisplayChecked,
  }
}

export const checksNodesAreConnected = (className) => {
  let status = false

  App.editor.nodes.forEach(node => {
    const allInputs = Array.from(node.inputs).some(([key, input]) => input.connections.length >= 1) // eslint-disable-line
    const allOutputs = Array.from(node.outputs).some(([key, output]) => output.connections.length >= 1) // eslint-disable-line

    if (false === allInputs && false === allOutputs) {
      status = true
      // return false
      addClassToNodeElement(className, node)
    }
  })

  return status
}

export const addClassIfNodesHaveNoValue = (className) => {
  let status = false
  const nodes = App.editor.nodes

  if (Array.isArray(nodes)) {
    for (const node of nodes) {
      const ignorableNodes = App.config.ignoreNodesWhileCheckingData
      if (ignorableNodes.length > 0) {
        if (ignorableNodes.indexOf(node.name) > -1) {
          continue;
        }
      }

      if (! hasNodeValue(node)) {
        if (false === status) {
          status = true
        }
  
        addClassToNodeElement(className, node)
      }
    }
  }
  
  return status
}

export const addClassIfOutputHasNoConnection = (componentName, socketName, className = '') => {
  let status = false
  App.editor.nodes.forEach(node => {
    if (componentName === node.name) {
      const connections = getNodeOutputConnectionsBySocketName(socketName, node)
      if (! connections.length) {
        if (CONST_COMPONENT.USER_INPUT_FLOW_SINGLE === node.name) {
          const connections2 = getNodeOutputConnectionsBySocketName(CONST_SOCKET.USER_INPUT_FLOW_SINGLE_OUTPUT_FINAL_REPLY, node)
          if (! connections2.length) {
            if (false === status) {
              status = true
            }
      
            if (className) {
              addClassToNodeElement(className, node)
            }
          }
        } else {          
          if (false === status) {
            status = true
          }
    
          if (className) {
            addClassToNodeElement(className, node)
          }
        }
      } else {
        if (className) {
          removeClassFromNodeElement(className, node)
        }
      }
    }
  })
  
  return status
}

const hasNodeValue = (node) => {
  if (0 === Object.keys(node.data).length) {
    return false
  }
  
  return true
}

export const warnIfSequenceSingleHasNonPromotionalValue = (className) => {
  let status = false

  App.editor.nodes.forEach(node => {
    if (CONST_COMPONENT.SEQUENCE === node.name) {
      const connections = getNodeOutputConnectionsBySocketName(CONST_SOCKET.SEQUENCE_OUTPUT, node)
      if (connections.length) {
        for (const connection of connections) {
          const inputNode = connection.input.node
          if (inputNode.data.isNonPromotionalChecked) {
            if (! node.data.closingTime || 
              ! node.data.messageTag || 
              ! node.data.startingTime || 
              ! node.data.timezone
            ) {
              if (false === status) {
                status = true
              }

              addClassToNodeElement(className, node)
            }
          }
        }
      }
    }
  })
  
  return status
}

export const hasNodeOutputEmptyConnection = (componentName, socketName) => {
  return App.editor.nodes.some(node => {
    if (componentName === node.name) {
      const connections = getNodeOutputConnectionsBySocketName(socketName, node)
      console.log(connections)
      return connections.length === 0
    }
  })
}

export const addClassToNodeElement = (className, node) => {
  if (node.vueContext && node.vueContext.$el) {
    if (! node.vueContext.$el.classList.contains(className)) {
      node.vueContext.$el.classList.add(className)
    }
  }
}

const removeClassFromNodeElement = (className, node) => {
  if (node.vueContext && node.vueContext.$el) {
    if (node.vueContext.$el.classList.contains(className)) {
      node.vueContext.$el.classList.remove(className)
    }
  }
}

