export const removeNodes = (nodesList) => {
  if (Array.isArray(nodesList)) {
    for (const node of nodesList) {
      if (node instanceof Rete.Node) {
        App.editor.removeNode(node)
      }
    }
  }
}

export const createNodeOnClickComponentList = async (node, event) => {
  /*eslint no-case-declarations: "error"*/
  /*eslint-env es6*/
  const inputSocketName = event.target.dataset.inputSocketName
  const componentName = event.target.dataset.componentName
  const output = node.output.node.outputs.get(node.output.key)
  
  switch(componentName) {
    case CONST_COMPONENT.CAROUSEL: {
      const carouselNode = await createCarouselAndChildrenNodes(event)
      const carouselNodeInput = carouselNode.inputs.get(inputSocketName)
      App.editor.connect(output, carouselNodeInput)
      break
    }
    case CONST_COMPONENT.GENERIC_TEMPLATE: {
      const genericTemplateNode = await createGenericTemplateAndChildrenNodes(event)
      const genericTemplateNodeInput = genericTemplateNode.inputs.get(inputSocketName)
      App.editor.connect(output, genericTemplateNodeInput)
      break
    }
    case CONST_COMPONENT.SEQUENCE: {
      const sequenceNode = await createSequenceAndChildrenNodes(event)
      const sequenceNodeInput = sequenceNode.inputs.get(inputSocketName)
      App.editor.connect(output, sequenceNodeInput)      
      break
    }
    case CONST_COMPONENT.POSTBACK: {
      const referenceNode = App.editor.nodes.find(item => item.name === CONST_COMPONENT.POSTBACK)
      if (referenceNode) {
        const referenceNodeInput = referenceNode.inputs.get(inputSocketName)
        App.editor.connect(output, referenceNodeInput)
      }
      break
    }
    default: 
      await createSingleNode(node, event)
  }

  App.editor.removeNode(node)
}

// eslint-disable-next-line
export const createCarouselAndChildrenNodes = async (event = null, ui = null) => {
  const carouselComponent = App.components.get(CONST_COMPONENT.CAROUSEL)
  const carouselComponentInstance = new carouselComponent(App.config)
  const carouselNode = await carouselComponentInstance.createNode()

  if (event) {
    App.editor.view.area.pointermove(event)
  }
  const { x, y } = App.editor.view.area.mouse
  carouselNode.position = [x, y]

  App.editor.addNode(carouselNode)

  const buttonComponent = App.components.get(CONST_COMPONENT.BUTTON)
  const carouselSingleComponent = App.components.get(CONST_COMPONENT.CAROUSEL_ITEM)
  
  const carouselSingles = [
    new carouselSingleComponent(App.config),
    new carouselSingleComponent(App.config),
    new carouselSingleComponent(App.config),
  ]
  
  carouselSingles.map(async (carouselSingle, index) => {
    const carouselSingleNode = await carouselSingle.createNode()
    const buttonInstance = new buttonComponent(App.config)
    const buttonNode = await buttonInstance.createNode()
    
    const carouselSingleY = fixYPositionOfChildComponents(index, 90, carouselSingles.length)
    carouselSingleNode.position = [x + 250, carouselSingleY]

    const buttonY = fixYPositionOfChildComponents(index, 130, carouselSingles.length)
    buttonNode.position = [x + 480, buttonY]
    
    App.editor.addNode(carouselSingleNode)
    App.editor.addNode(buttonNode)

    App.editor.connect(
      carouselNode.outputs.get(CONST_SOCKET.CAROUSEL_OUTPUT_ITEMS), 
      carouselSingleNode.inputs.get(CONST_SOCKET.CAROUSEL_SINGLE_INPUT)
    )
    
    App.editor.connect(
      carouselSingleNode.outputs.get(CONST_SOCKET.CAROUSEL_SINGLE_OUTPUT),
      buttonNode.inputs.get(CONST_SOCKET.BUTTON_INPUT)
    )
  })

  return carouselNode
}

// eslint-disable-next-line
export const createGenericTemplateAndChildrenNodes = async (event = null, ui = null) => {
  const genericTemplateComponent = App.components.get(CONST_COMPONENT.GENERIC_TEMPLATE)
  const genericTemplateComponentInstance = new genericTemplateComponent(App.config)
  const genericTemplateNode = await genericTemplateComponentInstance.createNode()
  
  if (event) {
    App.editor.view.area.pointermove(event)
  }
  const { x, y } = App.editor.view.area.mouse
  genericTemplateNode.position = [x, y]

  App.editor.addNode(genericTemplateNode)
  
  const buttonNode = await createButtonNode()
  buttonNode.position = [x + 250, y]

  App.editor.addNode(buttonNode)

  App.editor.connect(
    genericTemplateNode.outputs.get(CONST_SOCKET.GENERIC_TEMPLATE_OUTPUT_BUTTON),
    buttonNode.inputs.get(CONST_SOCKET.BUTTON_INPUT)
  )

  return genericTemplateNode
}

// eslint-disable-next-line
export const createSequenceAndChildrenNodes = async (event = null, ui = null) => {
  const sequenceComponent = App.components.get(CONST_COMPONENT.SEQUENCE)
  const sequenceComponentInstance = new sequenceComponent(App.config)
  const sequenceNode = await sequenceComponentInstance.createNode()
  
  if (event) {
    App.editor.view.area.pointermove(event)
  }
  const { x, y } = App.editor.view.area.mouse
  sequenceNode.position = [x, y]

  App.editor.addNode(sequenceNode)

  const items = [1, 2, 3]
  
  // eslint-disable-next-line
  items.map(async (item, index) => { 
    const sequenceSingleNodePositionY = fixYPositionOfChildComponents(index, 100, items.length)
    const squenceSingleNodePosition = [x + 250, sequenceSingleNodePositionY]
    const sequenceSingleNode = await addNewSequenceSingleToEditor(squenceSingleNodePosition)

    const childNewPostbackNodePositionY = fixYPositionOfChildComponents(index, 122, items.length)
    const childNewPostbackNodePosition = [x + 500, childNewPostbackNodePositionY]
    const childNewPostbackNode = await addNewPostbackNodeToEditor(childNewPostbackNodePosition)

    App.editor.connect(
      sequenceNode.outputs.get(CONST_SOCKET.SEQUENCE_OUTPUT),
      sequenceSingleNode.inputs.get(CONST_SOCKET.SEQUENCE_SINGLE_INPUT)
    )

    App.editor.connect(
      sequenceSingleNode.outputs.get(CONST_SOCKET.SEQUENCE_SINGLE_OUTPUT),
      childNewPostbackNode.inputs.get(CONST_SOCKET.NEW_POSTBACK_INPUT)
    )
  })

  return sequenceNode
}

const createSingleNode = async (node, event) => {
  const inputSocketName = event.target.dataset.inputSocketName
  const componentName = event.target.dataset.componentName

  const targetNode = await createNodeByComponentName(componentName)
  const { x, y } = App.editor.view.area.mouse
  targetNode.position = [x, y]

  const output = node.output.node.outputs.get(node.output.key)
  const input = targetNode.inputs.get(inputSocketName)

  App.editor.addNode(targetNode)
  App.editor.connect(output, input)
}

export const fixYPositionOfChildComponents = (index, componentHeight, totalComponents) => {
  let halfOfTotal = componentHeight * totalComponents / 2
  return (componentHeight * index) - (halfOfTotal - componentHeight / 2)
}

export const isFileTypeVerified = (file, allowedTypes) => {
  if ((file instanceof File) && Array.isArray(allowedTypes)) {
    if (allowedTypes.indexOf(file.type) > -1) {
      return true
    }
  }

  return false
}

export const addClassToAll = (className, querySelector) => {
  const items = document.querySelectorAll(querySelector)

  Array.from(items).forEach((item) => {
    if (! item.classList.contains(className)) {
      item.classList.add(className)
    }
  })
}

export const removeAllClasses = (className, querySelector) => {
  const items = document.querySelectorAll(querySelector)
  
  Array.from(items).forEach((item) => {
    if (item.classList.contains(className)) {
      item.classList.remove(className)
    }
  })
}

