export const templateComponentMethods = {
  hasData(node) {
    return node.data && (Object.values(node.data).length > 0) 
      ? 'node--has-data'
      : null
  },

  camelCase(name) {
    return camelCase(name)
  },

  startCase(name) {
    return startCase(name)
  },

  isSelected(node) {
    return App.editor.selected.contains(node) 
      ? 'node--selected' 
      : null
  },

  hasConnection(inputOrOutput) {
    return inputOrOutput.connections.length > 0
  },

  handleClick(node) {
    App.handleNodeData(node, App.editor)
  },


  async displayStat (node) {
    return await sleep(250).then(() => {
      const message_sent_stat_addon = window.xitFlowBuilderData.message_sent_stat_addon

      if (! message_sent_stat_addon) {
        return;
      }

      var message_sent_stat = window.xitFlowBuilderData.message_sent_stat

      var uniqueId = ''
      var is_message_sent = false
      // const targetNode = getNextNewPostback(node)

      if(node.name === CONST_COMPONENT.BUTTON)
      {
        uniqueId = node.data.postbackId
      }
      else if(node.name === CONST_COMPONENT.QUICK_REPLY)
      {
        uniqueId = node.data.uniqueId;
        is_message_sent = false
      }
      else
      {
        uniqueId = node.data.uniqueId;
        is_message_sent = true
      }

      var total_sent_click = Object.keys(message_sent_stat).indexOf(uniqueId) > -1 ? message_sent_stat[uniqueId].sent_click : 0
      var total_error = Object.keys(message_sent_stat).indexOf(uniqueId) > -1 ? message_sent_stat[uniqueId].total_error : 0
      var subscribers = Object.keys(message_sent_stat).indexOf(uniqueId) > -1 ? message_sent_stat[uniqueId].subscribers : 0
      var delivered = Object.keys(message_sent_stat).indexOf(uniqueId) > -1 ? message_sent_stat[uniqueId].delivered : 0

      let html = ''

      if (is_message_sent) {
        html += '<ul class="list-group list-group-horizontal list-group-horizontal-custom text-center">'
        html += '<li class="list-group-item"><span><i class="far fa-paper-plane text-info"></i> ' +lang.message_sent_lang+ '</span> <p>' +total_sent_click+ '</p></li>'
        html += '<li class="list-group-item"><span><i class="far fa-check-circle text-success"></i> '+lang.message_sent_stat_delivered+'</span> <p>'+delivered+'</p></li>'
        html += '<li class="list-group-item"><span><i class="far fa-user-circle text-primary"></i> '+lang.message_sent_stat_subscribers+'</span> <p>'+subscribers+'</p></li>'
        html += '<li class="list-group-item"><span><i class="fas fa-bug text-danger"></i> ' +lang.message_sent_stat_error_lang+ '</span> <p>' +total_error+ '</p></li>'
        html += '</ul>'
      }
      else {
        html += '<ul class="list-group list-group-horizontal list-group-horizontal-custom text-center">'
        html += '<li class="list-group-item"><span><i class="far fa-hand-pointer text-info"></i> ' +lang.postback_click_lang+ '</span> <p>' +total_sent_click+ '</p></li>'
        html += '<li class="list-group-item"><span><i class="far fa-user-circle text-primary"></i> '+lang.message_sent_stat_subscribers+'</span> <p>'+subscribers+'</p></li>'
        html += '<li class="list-group-item"><span><i class="fas fa-bug text-danger"></i> ' +lang.message_sent_stat_error_lang+ '</span> <p>' +total_error+ '</p></li>'
        html += '</ul>'
      }

      return html
    })
  }
}


export const hasInputConnectionWith = (node, nodeInputSocketName, OtherNodeOutputSocketName) => {
  const input = node.inputs.get(nodeInputSocketName)

  if (input instanceof Rete.Input) {
    const connections = input.connections
    if (Array.isArray(connections)) {
      for (const connection of connections) {
        if (connection.output.key === OtherNodeOutputSocketName) {
          return true
        }
      }
    }
  }

  return false
}

// https://stackoverflow.com/a/10311375
export const alertBeforeClosingBrowser = () => {
  window.onbeforeunload = function (e) {

    if(true === window.hasXitFlowBuilderData) {
      return
    }

    e = e || window.event

    // For IE and Firefox prior to version 4
    if (e) {
      e.returnValue = 'Sure?'
    }

    // For Safari
    return 'Sure?'
  }
}

export const showPerformanceNoticeForFirefox = (afterSeconds) => {
  setTimeout(async () => {      
    if (! document.cookie.split('; ').find(row => row.startsWith('xitFlowBuilderfirfoxPerformanceNotice'))) {
      const browser = getBrowser()
      const message = `You're using ${browser.name}. For better performace, we recommend use Chrome, Opera or Safari.`
      await Swal.fire({
        title: 'Info!',
        text: message,
        icon: 'info',
        confirmButtonText: 'Dont show again',
        showCancelButton: true,
        cancelButtonText: 'Cancel',
      })
      .then((result) => {
        if (result.isConfirmed) {
          document.cookie = "xitFlowBuilderfirfoxPerformanceNotice=true; SameSite=Strict; expires=Fri, 31 Dec 9999 23:59:59 GMT"
        }
      })
    }
  }, afterSeconds)
}

// https://stackoverflow.com/a/16938481
export const getBrowser = () => {
  var ua = navigator.userAgent,tem,M=ua.match(/(opera|chrome|safari|firefox|msie|trident(?=\/))\/?\s*(\d+)/i) || []; 
  if(/trident/i.test(M[1])) {
    tem = /\brv[ :]+(\d+)/g.exec(ua) || []
    return {
      name:'IE', 
      version:(tem[1] || '')
    }
  }

  if(M[1] === 'Chrome') {
    tem = ua.match(/\bOPR|Edge\/(\d+)/)
    if(tem != null) {
      return {
        name:'Opera', 
        version: tem[1]
      }
    }
  }

  M = M[2]? [M[1], M[2]]: [navigator.appName, navigator.appVersion, '-?']
  if((tem=ua.match(/version\/(\d+)/i)) != null) {
    M.splice(1, 1, tem[1])
  }

  return {
    name: M[0],
    version: M[1]
  }
}

export const sweetAlertCommonParams = (node, width = "19rem") => {
  const nodeName = camelCase(node.name)

  return {
    width: width,
    position: 'top-end',
    focusConfirm: false,
    buttonsStyling: false,
    showCancelButton: true,
    confirmButtonText: lang.swal_button_ok,
    cancelButtonText: lang.swal_button_cancel,
    customClass: {
      container: 'node-id-' + node.id + ' ' + nodeName,
      popup: 'p-2',
      header: 'p-0 mt-4',
      content: 'p-0',
      actions: 'd-flex justify-content-between p-0 m-0 pt-4 pb-4',
      footer: 'p-0',
      confirmButton: 'btn btn-outline-primary',
      denyButton: 'btn btn-outline-danger',
      cancelButton: 'btn btn-outline-secondary',
    },
    showClass: {
      popup: false,
    },
  }
}

export const removeConnectionsHavingUnmatch = (node, match, socketName) => {
  const nodeDataValues = Object.values(node.data)
  if (nodeDataValues.length > 0) {
    let breakStatus = false
    const connections = getNodeOutputConnectionsBySocketName(socketName, node)

    if (match.length > 0 && connections.length > 0) {
      for(const item of match) {
        if (! (nodeDataValues.indexOf(item) > -1)) {
          removeNodeConnections(connections)
        } else {
          breakStatus = true
        }

        if (breakStatus) {
          return
        }
      }
    }
  }
}

export const getNodeOutputConnectionsBySocketName = (socketName, node) => {
  const outputs = node.outputs.get(socketName)
  return (outputs && outputs.connections) ? outputs.connections : []
}

export const getNodeInputConnectionsBySocketName = (socketName, node) => {
  const inputs = node.inputs.get(socketName)
  return (inputs && inputs.connections) ? inputs.connections : []
}

const removeNodeConnections = (connections) => {
  connections.map(singleConnection => {
    App.editor.removeConnection(singleConnection)
  })
}

export const updateNodeConnections = (node, timeout = 100) => {
  setTimeout(() => {
    App.editor.view.updateConnections({ node })
    // node.update()
  }, timeout)
}

export const getSelectOptionsFromArrayOfObjects = (optionsArray, defaultValue = '') => {
  let html = ''
  for (const option of optionsArray) {
    if ('string' === typeof option) {
      if (defaultValue === option) {
        html += '<option value="' + option + '" selected>' + option + '</option>'
      } else {
        html += '<option value="' + option + '">' + option + '</option>'
      }
    } else if ('object' === typeof option) {
      if (defaultValue === option.key) {
        html += '<option value="' + option.key + '" selected>' + option.value + '</option>'
      } else {
        html += '<option value="' + option.key + '">' + option.value + '</option>'
      }
    }
  }

  return html
}

export const getSelectOptionsFromObject = (optionsObject, defaultValue = '') => {
  let html = ''
  const options = Object.keys(optionsObject);
  for (const option of options) {
    if (defaultValue === option) {
      html += '<option value="' + option + '" selected>' + optionsObject[option] + '</option>'
    } else {
      html += '<option value="' + option + '">' + optionsObject[option] + '</option>'
    }
  }

  return html
}

export function isPlainObject(obj) {
  return obj ? obj.constructor === {}.constructor : false
}

/**
 * Get file mime type from URL
 * 
 * @param {string} url
 * @param {function} callback 
 */
export function getMimeTypeFromUrl(url) {
  let mimeType = null
  const xhr = new XMLHttpRequest()

  xhr.open('HEAD', url)
  xhr.onreadystatechange = function () {
    if (this.readyState == this.DONE) {
      mimeType = this.getResponseHeader("Content-Type")
    }
  }

  xhr.send()

  return mimeType
}

const getUniqueId = () => {
  let count = 5
  return () => count++
}

export const uniqueId = getUniqueId()

export function hasDuplicates(array) {
  return (new Set(array)).size !== array.length;
}

export const createSelect2DynamicOption = (element) => {
  var page_table_id = window.xitFlowBuilderData.page_table_id
  var base_url = window.xitFlowBuilderData.base_url
  var instagram_bot_addon = window.xitFlowBuilderData.instagram_addon
  const social_media = instagram_bot_addon ? 'ig' : 'fb'

  $(element).select2({
    tags: true,
    width: '100%',
    dropdownParent: $('#swal2-content'),
  }).on('select2:select', function(e) {
    const data = e.params.data
    const tagsList = $(this).select2('data')

    if (tagsList.find(val => (val.selected === true && val.text === data.text)) !== undefined) {
      tagsList.push({ id: data.id, text: data.text })
      $(element).select2('data', tagsList)
    } else { 
      $.ajax({
        type:'POST',
        dataType:'JSON',
        url: base_url + "home/common_create_label_and_assign",
        data:{ page_id: page_table_id, label_name: data.text, social_media },
        success: function(response) {
          if(response.error) {
            Swal.showValidationMessage('Could not create label.')
          } else {
            const isDummyOptionAdded = $(element).find("option[value='" + data.text + "']")
            if (isDummyOptionAdded) {
              isDummyOptionAdded.remove();
            }
            
            const newOption = new Option(response.text, response.id, true, true)
            $(element).append(newOption).trigger('change');
          }
        }
      })
    } 
  })
}

export const makeAjaxCall = (callback, data) => {
  const parameters = {
    url: data.url,
    dataType: 'JSON',
    success: callback,
  }

  if (data.method && ('get' === data.method.toLowerCase())) {
    parameters.type = 'GET'
  } else {
    parameters.type = 'POST'
    parameters.data = data.data
  }

  $.ajax(parameters)
}

