export const selectedOptionsTextArray = (selectedOptions) => {
  const optionsTextArray = []
  if (Object.keys(selectedOptions).length > 0) {
    for (const text of selectedOptions) {
      optionsTextArray.push(text.label)
    }
  }

  return optionsTextArray
}

export const prepareDropdownLists = (response) => {
  let html = '';

  if (Array.isArray(response)) {
    response.forEach((val, idx) => { // eslint-disable-line
      html += '<span class="dropdown-item">' + val.value + '</span>'
    })
  }

  return html;
}

export const setDefaultComponentBasedOnUrlParams = (params) => {
  const delegator = parseDelegatorClassFromUrlParams(params)
  if (! (delegator instanceof CreateDefaultComponent)) {
    throw new Error('Invalid "type" param provided.')
  }

  delegator.setupDefaultComponent()
}

const parseDelegatorClassFromUrlParams = (params) => {
  if (! params.type) {
    throw new Error('"Type" was not provided in URL params.')
  }

  const config = App.config.defaultComponentMappingTypeToClass
  const delegatorClass = config[params.type] || null

  return delegatorClass ? new delegatorClass(params) : null
}

export const sleep = (seconds) => {
  return new Promise(resolve => setTimeout(resolve, seconds))
}

export const hasUrlParamType = () => {
  const params = getUrlParams()
  return (params && params.type) 
    ? Object.keys(App.config.defaultComponentMappingTypeToClass).indexOf(params.type) > -1
    : false
}

export const getUrlParams = () => {
  const urlSearchParams = new URLSearchParams(window.location.search)
  return Object.fromEntries(urlSearchParams.entries())
}

export const getFilteredActionButtonActionsList = (oldData) => {

  let actionsList = []

  if ((oldData && oldData.actionType) &&
    (oldData.actionType === 'broadcasting' ||
    oldData.actionType === 'messenger_engagement_plugin')
  ) {

    actionsList = actionButtonActionsList.filter((val) => {
      return val.key === oldData.actionType
    })

  } else if (hasUrlParamType()) {

    const params = getUrlParams()

    actionsList = actionButtonActionsList.filter((val) => {
      return val.key === params.type
    })

  } else {

    actionsList = actionButtonActionsList.filter((val) => {
      if (val.key === 'broadcasting') {
        return false
      } else if (val.key === 'messenger_engagement_plugin')  {
        return false
      }

      return true
    })
  }

  return actionsList
}