import Rete from 'rete'

import RcnSingleTemplate from './RcnSingleTemplate'
import CONST_SOCKET from '../../const/socket'
import CONST_COMPONENT from '../../const/component'
import lingualName from '../../const/lingualName'
import { lang } from '../../assets/lang'

export default class RcnSingleComponent extends Rete.Component {
  constructor(config) {
    super(CONST_COMPONENT.RCN_SINGLE)
    
    this.data.component = RcnSingleTemplate
    this.socket = config ? config.sockets : null
    this.isSequenceAddonAvailable = !! window.xitFlowBuilderData.sequence_addon
  }

  builder(node) {
    node.lingualName = lingualName.RCN_SINGLE
    this.setConnection()
    
    const rcnSingleInput = new Rete.Input(CONST_SOCKET.RCN_SINGLE_INPUT, lang.Rcn_single_socket_input_reply, this.socket.rcnSingleInput, false)
    const rcnSingleOutput = new Rete.Output(CONST_SOCKET.RCN_SINGLE_OUTPUT, lang.Rcn_single_socket_output_next, this.socket.rcnSingleOutput, false)
    
    node.addInput(rcnSingleInput)
    node.addOutput(rcnSingleOutput)
    
    if (this.isSequenceAddonAvailable) {
      const rcnSingleOutputSequence = new Rete.Output(
        CONST_SOCKET.RCN_SINGLE_OUTPUT_SEQUENCE, 
        lang.rcn_single_socket_output_next_sequence, 
        this.socket.rcnSingleOutputSequence, 
        false
      )

      node.addOutput(rcnSingleOutputSequence)
    }

    return node
  }

  // eslint-disable-next-line
  worker(node, inputs, outputs) {
    // outputs[CONST_SOCKET.RCN_SINGLE_OUTPUT] = node.data
  }

  setConnection() {
    this.socket.rcnSingleOutput.combineWith(this.socket.newPostbackInput)
    
    if (this.isSequenceAddonAvailable) {
      this.socket.rcnSingleOutputSequence.combineWith(this.socket.sequenceInput)
    }
  }
}