        <p 
          class="text-center mb-2 postback_title"
          v-if="node.data.actionType && node.data.actionTypeText"
        >{{ node.data.actionTypeText }}</p>

        <div v-if="node.data.actionType && 'messenger_engagement_plugin' === node.data.actionType">
          <div class="list-group" v-if="node.data.pluginType && ('checkbox_plugin' === node.data.pluginType || 'send_to_messenger' === node.data.pluginType)">
            <span class="list-group-item list-group-item-action flex-column align-items-start">
              <div class="d-flex w-100 justify-content-between">
                <small class="text-muted">{{ plugin_type }}</small>
                <h6 class="mb-1 card-data-title">{{ node.data.pluginTypeText || '' }} </h6>              
