            <span class="list-group-item list-group-item-action flex-column align-items-start" v-if="'checkbox_plugin' === node.data.pluginType">
              <div class="d-flex w-100 justify-content-between">
                <small class="text-muted">{{ center_align }} </small>
                <h6 class="mb-1 card-data-title">{{ 'true' === node.data.pluginCenterAlign ? center_align_yes : center_align_no }} </h6>              
