              <div class="d-flex w-100 justify-content-between">
                <small class="text-muted">{{ optin_success_message_in_website }}</small>
                <h6 class="mb-1 card-data-title">{{  node.data.optInSuccessMessage }} </h6>              
              </div>           
            </span> -->

