              <div class="d-flex w-100 justify-content-between">
                <small class="text-muted">{{ checkbox_validation_error_message }}</small>
                <h6 class="mb-1 card-data-title">{{ node.data.checkboxValidationErrorMessage }} </h6>              
              </div>           
            </span> -->

          </div>

