            <span class="list-group-item list-group-item-action flex-column align-items-start"  v-if="node.data.chatPluginLoadingDelayInSeconds">
              <div class="d-flex w-100 justify-content-between">
                <small class="text-muted">{{ loading_delay_seconds }}</small>
                <h6 class="mb-1 card-data-title">{{ node.data.chatPluginLoadingDelayInSeconds + ' sec' }} </h6>              
              </div>           
            </span>

            <span class="list-group-item list-group-item-action flex-column align-items-start"  v-if="node.data.chatPluginThemeColor">
              <div class="d-flex w-100 justify-content-between">
                <small class="text-muted">{{ theme_color }}</small>
                <h6 class="mb-1 card-data-title">{{ node.data.chatPluginThemeColor }} </h6>              
              </div>           
            </span>

