            <Socket v-socket:output="output" type="output" :socket="output.socket"></Socket>
          </div>
        </div>
        <div class="input_content">
          <div class="input text-left" v-for="input in inputs()" :key="input.key" :class="hasConnection(input) ? 'has-connection' : ''">
            <Socket v-socket:input="input" type="input" :socket="input.socket"></Socket>
            <div class="input-title">{{input.name}}</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import VueRenderPlugin from 'rete-vue-render-plugin'

import { lang } from '../../assets/lang'
import { templateComponentMethods } from '../../utils/functions'

export default {
  name: 'AudioTemplate',
  mixins: [VueRenderPlugin.mixin],
  components: {
    Socket: VueRenderPlugin.Socket
  },

  data: () => ({
    baseUrl: window.xitFlowBuilderData.base_url,
    
    plugin_type: lang.action_button_html_plugin_type,
    domain: lang.action_button_html_domain,
    language: lang.action_button_html_language,
    cta_button_text: lang.action_button_html_cta_button_text,
    plugin_skin: lang.action_button_html_plugin_skin,
    plugin_skin_light: lang.action_button_html_plugin_skin_light,
    plugin_skin_dark: lang.action_button_html_plugin_skin_dark,
    plugin_skin_blue: lang.action_button_html_plugin_skin_blue,
    center_align: lang.action_button_html_center_align,
    center_align_yes: lang.action_button_html_center_align_yes,
    center_align_no: lang.action_button_html_center_align_no,
    plugin_size: lang.action_button_html_plugin_size,

    optin_success_redirect_url: lang.action_button_html_optin_success_redirect_url,
    optin_success_message_in_website: lang.action_button_html_optin_success_message_in_website,

    button_text: lang.action_button_html_button_text,
    button_url: lang.action_button_html_button_url,
    button_background_color: lang.action_button_html_button_background,
    button_text_color: lang.action_button_html_button_text_color,
    button_hover_background_color: lang.action_button_html_button_hover_background,
    button_text_hover_color: lang.action_button_html_button_text_hover_color,
    button_size: lang.action_button_html_button_size,

    checkbox_validation_error_message: lang.action_button_html_checkbox_validation_error_message,

    chat_plugin_loading: lang.action_button_html_chat_plugin_loading,
    loading_delay_seconds: lang.action_button_html_loading_delay_seconds,
    theme_color: lang.action_button_html_theme_color,
    do_not_show_if_not_logged_in: lang.action_button_html_do_not_show_if_not_logged_in,
    greeting_text_if_logged_in_to_facebook: lang.action_button_html_greeting_text_if_logged_in_to_facebook,
    greeting_text_if_not_logged_in_to_facebook: lang.action_button_html_greeting_text_if_not_logged_in_to_facebook,
    reference: lang.action_button_html_reference,
    labels: lang.action_button_html_select_labels,
  }),

  methods: {
    ...templateComponentMethods,
  }
}
</script>

<style scoped>
.card-data-media-audio {border-radius: 7px;}
.input_content { width:50% !important;margin-top: 25px;}
.output_content{ width:50% !important;float:right !important;}
.card-sockets { padding: 5px 0 !important;}
.card-data-media { font-size: 14px; }

</style>